.item{
    position: relative;
    border-radius: 2rem;
    box-shadow: 0 0 1rem black;
    overflow: hidden;
    display: flex;
}
.item img{
    width: 100%;
    transition: all .5s ease;
}
.item:hover img{
    transform: scale(1.1);
}
.layer{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.1), #0EF);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 4rem;
    transform: translateY(100%);
    transition: .5s ease;
}
.item:hover .layer{
    transform: translateY(0%);
    cursor: default;

}

.layer h4{
    font-size: 3rem;
}
.layer p{
    font-size: 1.6rem;
    margin: .3rem 0 1rem;
}
.layer a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background: #fff;
    border-radius: 50%;
}
.layer a i{
    font-size: 2rem;
    color: black;
}
@media (max-width:768.98px) {
    .layer h4{
        font-size: 4rem;
    }
    .layer p{
        font-size: 2.4rem;
        margin: 1rem 0 1rem;
    }
    .layer a{
        width: 10rem;
        height: 7rem;
    }
    .layer a i{
        font-size:5rem
    }
}