.list{
    display: grid;
    grid-template-rows: 1fr;
    gap: 2.5rem;
}
.groupone,.grouptwo{
    display: grid;
    gap: 2.5rem;

}
.groupone{
    grid-template-columns: 8fr 4fr;
}
.grouptwo{
    grid-template-columns: 4fr 4fr 4fr;

}
.groupthree{

}
.content{
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
}

@media (max-width:768.98px) {
    .list{
        gap: 5.5rem;
    }
    .groupone,.grouptwo{
        gap: 5.5rem;
    }
    .groupone{
        grid-template-columns: 1fr;
        
    }
    .grouptwo{
        grid-template-columns: 1fr;
    
    }
    .groupthree{
    
    }
    .content{
        gap: 5.5rem;
    }
}