@import "./../../app/assets/styles/components/variable";
.about{
    background-color: #1f242d8e;
// position: relative;
padding-top: 10rem;
padding-bottom: 10rem;
}
.wrapper{
    max-width: 120rem;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.description{
    
}
.photo{
    width: 40rem;
}
.photo img{
width: 100%;
}

.description h3{}
.description h4{
    font-size: 2.3rem;
    margin-top: 2rem;
}
.description p{
    margin-top: 3rem;
    font-size: 1.6rem;
    max-width: 60rem;
    word-spacing: 0.5rem;
    line-height: 1.5;
}

@media (max-width:768.98px) {
    .about{
        background-color: #1f242d8e;
        padding-left: 3rem;
        padding-right: 3rem;
        padding-bottom: 10rem;
        padding-top: 10rem;
    }
    .photo{
        display: none;
    }
    .wrapper{
        
        flex-direction:column;        
        align-items: flex-start;
    }
    .description{
    width: 100%;
    }
    .description h3{
        margin-bottom: 10rem;
    }
    .description h4{
        font-size: 4.3rem;
    }
    .description p{
        margin-top: 4rem;
        font-size: $textmobP;
    }

    .description{
        margin-top: 20rem;
    }

}