@import "./../../../app/assets/styles/components/variable";
header {
  animation: bg-animation 25s ease-in-out infinite;
  height: 80rem;
  // position: relative;
  // z-index: 222;
}


.wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 120rem;
  margin: 0 auto;
  padding-top: 20rem;
}
.description {
  color: wheat;
  width: 100%;

}
.description h3 {
  font-size: 5rem;
}
.description h1 {
  font-size: 6rem;
  margin-top: 3rem;
  color: #0EF;
}

.description p {
  font-size: 1.8rem;
  margin-top: 3rem;
  max-width: 50rem;
  word-spacing: 0.5rem;
  line-height: 1.5;
}

.ramka {
  width: 40rem;
  height: 30rem;
  border: 0.7rem solid #0EF;
  background: #0EF;
  border-radius: 30% 70% 70% 30%/30% 30% 70% 70%;
  box-shadow: 15px 15px 50px #0EF;
  animation: anim-ramka-1 10s infinite;

  font-family: "Roboto", sans-serif;
  overflow: hidden;
}
.ramka img {
  width: 40rem;
  position: relative;
  top: 3rem;
  left: -15%;
}
@media (orientation: portrait) and (max-width: 768.98px) {
  header {
    height: 95rem;
  }
}
// @media (orientation: portrait) and (min-width: 769px) {
//   header {
//     height: 95rem;
//   }
// }
@media (orientation: landscape) and (max-width: 768.98px) {
  header {
    height: 95rem;
  }
}
// @media (orientation: landscape) and (min-height: 500px) and (max-height: 1025px) {
//   header {
//     height: 95rem;
//   }
// }
@media (min-width:769px) {
  .mtop{
    padding-top: 26rem!important;
  }
}
@media (max-width: 768.98px) {
  header {
  }

  .wrapper {
    flex-direction: column;
    align-items: center;
    margin-left: 3rem;
    margin-right: 3rem;
    gap: 10rem;
  }
  .description h3 {
    font-size: $texth3;
  }
  .description h1 {
    font-size: 6rem;
  }
  .description {
    
  }
  .description h3{}
  .description h1{}
  .description p{
    font-size: $textmobP;
  }
  .ramka {
    width: 50rem;
    height: 40rem;
  }
  .ramka img {
    width: 50rem;
    left: 0px ;

  }
}



@keyframes anim-ramka-1 {
  0% {
    border-radius: 30% 70% 70% 30%/30% 30% 70% 70%;
    box-shadow: 15px 15px 50px #0EF;
  }
  25% {
    border-radius: 58% 42% 75% 25%/76% 46% 54% 24%;
  }
  50% {
    border-radius: 50% 50% 33% 67%/55% 27% 73% 45%;
    box-shadow: -10px -5px 50px #0EF;
  }
  75% {
    border-radius: 33% 67% 58% 42%/63% 68% 32% 37%;
  }
}



/* /////////////button////////////// */
.downloadSV{
  margin-top: 4rem;
}
.button{
  padding: 1rem 3rem;
  background: none;
  font-size: 1.8rem;
  color: wheat;
  border: 1px solid wheat;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.6s ease;
}
.button a{
  color: #0EF;
}
.button:hover{
border: 1px solid #5a493c;
}
@media (max-width:768.98px) {
  .button{
    font-size: 4.2rem;
  }
}
